import { Box, Checkbox, Heading, Input, Text } from 'native-base';

import { RoutineEntry, RoutineSet } from '../../domain/programs';
import { ScheduledWorkout } from '../../domain/schedule';
import { Table, TableHeader, TableRow } from '../Content';
import { ExerciseInfo } from './components/ExerciseInfo';

const SetIdentifier = ({ warmup, setId }: { warmup?: boolean; setId: number }) => (
  <Box width={25} borderRadius={10} background={warmup ? 'primary.50' : 'primary.200'} padding={0.5}>
    <Text textAlign={'center'} fontSize={'xs'}>
      {setId + 1}
    </Text>
  </Box>
);

const getTargetRepsPlaceholder = (entry: RoutineSet): string => {
  let placeholder = '';
  if (entry.minReps) {
    placeholder = `${entry.minReps}-${entry.maxReps}`;
  } else if (entry.targetReps) {
    placeholder = `${entry.targetReps}`;
  } else if (entry.reps) {
    placeholder = `${entry.reps}`;
  }

  if (entry.AMRAP) placeholder = `${placeholder}+`;

  return placeholder;
};

export const WorkoutSheet = ({
  workout,
  getRecommendedWeight,
  onChangeSetWeight,
  onChangeSetReps,
  onSetComplete,
  getPreviousLift,
}: {
  workout: ScheduledWorkout;
  getRecommendedWeight: (exerciseId: number, setId: number, warmup?: boolean) => number | undefined;
  onChangeSetWeight: (exerciseId: number, setId: number, value: number) => void;
  onChangeSetReps: (exerciseId: number, setId: number, value: number) => void;
  onSetComplete: (exerciseId: number, setId: number, isWarmup: boolean) => void;
  getPreviousLift: (
    exerciseId: number,
    setId: number,
    isWarmup: boolean
  ) => { weight: number | undefined; reps: number } | undefined;
}) => {
  const getPreviousLabel = (exerciseId: number, setId: number, isWarmup = false) => {
    const prev = getPreviousLift(exerciseId, setId, isWarmup);
    if (prev && prev.weight) {
      return `${prev?.weight}x${prev?.reps}`;
    } else if (prev && prev.reps) {
      return `${prev?.reps}`;
    }
    return '-';
  };
  return (
    <>
      {workout.routine.map((item: RoutineEntry, exerciseId: number) => {
        const key = `workout-${item.exercise}`;
        return (
          <Box key={key} marginBottom={10}>
            <Heading fontSize={'md'} fontWeight={400} marginY={2}>
              {item.optional && <Text italic>(Optional) </Text>}
              {item.exercise} <ExerciseInfo item={item} />
            </Heading>
            {item.note && <Text>Note: {item.note}</Text>}
            <Table>
              <TableHeader
                headings={[
                  { content: 'Set', textAlign: 'left', width: 10 },
                  { content: 'Previous' },
                  ...(!item.bodyweight ? [{ content: 'Weight' }] : []),
                  { content: 'Reps' },
                  { content: '' },
                ]}
              />
              {item.warmup?.map((entry: RoutineSet, setId: number) => (
                <TableRow
                  key={`${key}-warmup-${setId}`}
                  values={[
                    { width: 10, content: <SetIdentifier warmup setId={setId} /> },
                    { content: getPreviousLabel(exerciseId, setId, true), textAlign: 'center' },
                    ...(!item.bodyweight
                      ? [{ content: getRecommendedWeight(exerciseId, setId, true), textAlign: 'center' }]
                      : []),
                    { content: entry.reps, textAlign: 'center' },
                    {
                      textAlign: 'right',
                      content: (
                        <Checkbox
                          marginLeft={'auto'}
                          isChecked={entry.status === 'done'}
                          value={'done'}
                          onChange={(checked: boolean) => checked && onSetComplete(exerciseId, setId, true)}
                        >
                          <Text display={'none'}>Done</Text>
                        </Checkbox>
                      ),
                    },
                  ]}
                />
              ))}
              {item.sets.map((entry: RoutineSet, setId: number) => (
                <TableRow
                  key={`${key}-set-${setId}`}
                  values={[
                    { width: 10, content: <SetIdentifier setId={setId} /> },
                    { content: getPreviousLabel(exerciseId, setId), textAlign: 'center' },
                    ...(!item.bodyweight
                      ? [
                          {
                            content: (
                              <Input
                                keyboardType={'decimal-pad'}
                                value={entry.weight?.toString()}
                                textAlign={'center'}
                                // @ts-expect-error if force number it does not allow decimals
                                onChangeText={(value: string) => onChangeSetWeight(exerciseId, setId, value)}
                                placeholder={getRecommendedWeight(exerciseId, setId)?.toString()}
                                marginX={1}
                              />
                            ),
                            textAlign: 'center',
                          },
                        ]
                      : []),
                    {
                      content: (
                        <Input
                          keyboardType={'number-pad'}
                          value={entry.reps?.toString()}
                          textAlign={'center'}
                          onChangeText={(value: string) => onChangeSetReps(exerciseId, setId, Number(value))}
                          placeholder={getTargetRepsPlaceholder(entry)}
                          marginX={1}
                        />
                      ),
                      textAlign: 'center',
                    },
                    {
                      textAlign: 'right',
                      content: (
                        <Checkbox
                          marginLeft={'auto'}
                          isChecked={entry.status === 'done'}
                          value={'done'}
                          onChange={(checked: boolean) => checked && onSetComplete(exerciseId, setId, false)}
                        >
                          <Text display={'none'}>Done</Text>
                        </Checkbox>
                      ),
                    },
                  ]}
                />
              ))}
            </Table>
          </Box>
        );
      })}
    </>
  );
};
